import {Me} from './me';
import {Header} from './header';
import React, { useState,useEffect} from "react";
import image1 from './images/Screenshot_1.jpg';
import image2 from './images/Screenshot_2.jpg';
import image3 from './images/Screenshot_3.jpg';
import image4 from './images/Screenshot_4.jpg';
import image5 from './images/Screenshot_5.jpg';
import image6 from './images/Screenshot_6.jpg';
import image7 from './images/Screenshot_7.png';
import image8 from './images/Screenshot_8.png';
import image9 from './images/Screenshot_9.png';
import image10 from './images/Screenshot_10.png';
import image11 from './images/Screenshot_11.png';
import image12 from './images/Screenshot_12.png';
import usePageTracker from './usePageTracking';
import { gsap } from "gsap"

export const Projects = ()=> {
	useEffect(() => {
		document.title = "Projects"; 
        gsap.to('.info_container', {
          duration: 2,
          "--clip": '100%'
        })
	}, []);
	const images = [image8, image9, image10, image11, image12]
	var randomImage = images[Math.floor(Math.random()*images.length)]
	const [time, setTime] = useState(Date.now());
	
	useEffect(() => {
		const interval = setInterval(() => setTime(Date.now()), 4000);
		return () => {
			clearInterval(interval);
		};
        
	}, []);

	usePageTracker();
	return (
		<>
			<Header/>
			<div className='main'>
				<Me/>
				<div className="info_container">
					<div className="page_break">
						<a href="https://dydx.oneshoo.com" style={{cursor: 'pointer'}}  target="_blank" rel="noopener noreferrer">
                            <div className="photo_container">
                                <img id="project_image" src={randomImage} className="screenshot" alt="" />
                            </div>
                        </a>
						Developed by Oneshoo in conjunction with Llama for dYdX.<br/> Role: Front-End Developer. April-May 2022<br/> 
                        <a href="https://dydx.oneshoo.com" style={{cursor: 'pointer'}}>https://dydx.oneshoo.com</a><br/>
                        Technologies used:
                         ReactJS,
                         Vuexy,
                         Bootstrap,
                         ApexCharts.js,
                         Chart.js,
                         Recharts,
                         Flatpickr,... 
					</div>
                     <div className="page_break">
                        <div className="photo_container"><img src={image5} className="screenshot" alt="" /></div>
                            A photographer's website<br/>
                            HTML, CSS
                        </div>
                    <div className="page_break">
					
                    <div className="page_break">
						<div className="photo_container">
							<img id="project_image" src={image3} className="screenshot" alt="" />
						</div>
						Web application in which after login/registration you can save all about important people (and animals) in your life<br/>
						HTML, CSS, JS, PHP, MySQL, JQuery, AJAX, PHPMailer, Flickity
					</div>
                   <div className="photo_container">
						<img src={image6} className="screenshot" alt="" />
					</div>
					Me trying to sell myself!<br/>
					ReactJS, Bootstrap
					</div>
					<div className="tobecontinued"> To be continued <br/>. . .
					</div>
				</div>
			</div>
		</>  
	)
}