import {Me} from './me';
import {Header} from './header';
import React, { useEffect} from "react";
import usePageTracker from './usePageTracking';
import { gsap } from "gsap"

export const Main = ()=>{
	useEffect(() => {
		document.title = "Dajana Rosic-Manceva"; 
        gsap.to('.info_container', {
          duration: 2,
          "--clip": '100%'
        })
        
	}, []);
	usePageTracker();
	return (
		<>
			<Header/>
			<div className='main'>
				<Me/>
				<div className="info_container">
					<div className="headline">
						Welcome!
					</div><br/><br/>
					<div className="page_break">
						Here you can find out more about me and my recent projects<br/>
					</div>
					<div>
						This website is made with ReactJS and Bootstrap,<br/> published with npm on GitHub Pages.
					</div>
				</div>
			</div>
		</>
	)
}